.spas_heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.spas_heading > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: rgba(18, 18, 18, 0.8);
  margin: 0;
}
.spas_container {
  display: flex;
  margin-top: 0.5rem;
  gap: 3vw;
  justify-content: center;
}
.spasc_inner {
  display: flex;
  align-items: center;
  width: 7vw;
  cursor: pointer;
  justify-content: space-between;
}
.spasc_inner > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #121212;
  margin: 0;
}
.spasd_card {
  margin-top: 0.5rem;
  margin-left: -0.6rem;
  margin-right: -0.6rem;
}
.spasdc_inner {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  align-items: center;
  background-color: var(--bg-attendance-stats);
}
.spasdc_inner > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #121212;
  margin: 0;
}

/* // student profile about  */
.sdpa {
  padding: 1rem;
}
/* .sdpa_section {
} */
.sdpa_section > h5 {
  font-weight: var(--font-weight-600);
  line-height: var(--line-height-17);
  font-size: 15px;
  color: rgba(18, 18, 18, 0.9);
  margin: 0;
  margin-bottom: 16px;
}
.sdpac_wrap {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 2%;
  flex-wrap: wrap;
}
.sdpas_container {
  width: 49%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  word-break: break-all;
  margin-bottom: 0.4rem;
  /* word-wrap: break-word; */
}
.sdpasc_label {
  color: rgba(18, 18, 18, 0.5);
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-14);
  line-height: var(--line-height-17);
  margin: 0;
}
.sdpasc_value {
  color: rgba(18, 18, 18, 0.9);
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-14);
  line-height: var(--line-height-17);
  margin: 0;
  word-break: break-word;
}
